.test-wrapper--status {
  padding: 40px 40px 10px 0;
  margin: 0;
  border-top: solid 1px #bfc6cc;
  text-align: center;
  list-style-type: none;
  position: absolute;
  bottom: 20px;
  left: 40px;
  right: 40px;
}
@media screen and (max-width: 890px) {
  .test-wrapper--status {
    left: 20px;
    right: 20px;
  }
}
.test-wrapper--status li {
  display: inline-block;
  margin: 0;
  width: 12.5%;
  min-width: 30px;
  height: 30px;
  text-align: center;
  position: relative;
}
.test-wrapper--status li::before, .test-wrapper--status li::after {
  content: "";
  display: block;
  width: 50%;
  height: 4px;
  position: absolute;
  top: calc(50% - 2px);
  left: 0;
  background-color: #f3f3f3;
  z-index: -1;
  transition: background-color 0.4s;
}
.test-wrapper--status li i {
  width: 30px;
  height: 30px;
  background-image: url("images/icons__dot--grey.svg");
  background-position: center;
  background-repeat: no-repeat;
  display: inline-block;
  transition: background 0.4s;
}
.test-wrapper--status li::after {
  left: inherit;
  right: 0;
}
.test-wrapper--status li.seen i {
  background-image: url("images/icons__dot--red.svg");
}
.test-wrapper--status li.seen::after, .test-wrapper--status li.seen::before {
  background-color: #dc1c24;
}
.test-wrapper--status li.active i {
  background-image: url("images/icons__dot--red.svg");
}
.test-wrapper--status li.active::before {
  background-color: #dc1c24;
}
.test-wrapper--status li.final i {
  background-image: url("images/icons__heart--grey.svg");
}
.test-wrapper--status li.final.active i {
  background-image: url("images/icons__heart--red.svg");
}
.test-wrapper--status li:first-child::before {
  display: none;
}
.test-wrapper--status li:last-child::after {
  display: none;
}

.layout-main ul.test-wrapper--status {
  margin-left: 0;
}

