.test-wrapper .result__group--basis .button-wrapper .primary, .test-wrapper .result__group--1 .button-wrapper .primary, .test-wrapper .result__group--2 .button-wrapper .primary, .test-wrapper .result__group--5 .button-wrapper .primary {
