.test-wrapper {
  padding: 100px 40px 0;
  border: solid 3px #bfc6cc;
  background-color: none;
  position: relative;
  overflow: hidden;
  height: 660px;
}
.test-wrapper .test-wrapper-body {
  text-align: center;
}
.test-wrapper .transition {
  -webkit-transition: opacity 900ms ease-in-out;
  -moz-transition: opacity 900ms ease-in-out;
  -ms-transition: opacity 900ms ease-in-out;
  -o-transition: opacity 900ms ease-in-out;
  transition: opacity 900ms ease-in-out;
  opacity: 0;
}
.test-wrapper .transition.entered {
  opacity: 1;
}
.test-wrapper .transition.exiting {
  opacity: 0;
}
.test-wrapper h2 {
  font-size: 22px;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 20px;
  line-height: 1.4;
}
.test-wrapper p {
  margin-bottom: 20px;
  margin-top: 0;
  font-size: 16px;
  line-height: 1.8;
}
@media screen and (max-width: 890px) {
  .test-wrapper p {
    font-size: 14px;
    line-height: 1.4;
  }
}
.test-wrapper p:last-child {
  margin-bottom: 0;
}
.test-wrapper--intro, .test-wrapper--step, .test-wrapper--result {
  padding: 0 0 60px;
}
.test-wrapper button,
.test-wrapper .result__link,
.test-wrapper .result a {
  -webkit-transition: background-color 100ms ease-in-out;
  -moz-transition: background-color 100ms ease-in-out;
  transition: background-color 100ms ease-in-out;
  border: solid 2px;
  border-radius: 100px;
  display: inline-block;
  padding: 0 20px;
  font-size: 0.75rem;
  line-height: 36px;
  font-weight: 700;
  text-decoration: none;
  min-width: 200px;
  text-align: center;
  margin: 20px 10px 0;
  cursor: pointer;
  outline: none;
  border-color: #1a1a1a;
  color: #1a1a1a;
  background-color: transparent;
}
@media screen and (max-width: 620px) {
  .test-wrapper button,
.test-wrapper .result__link,
.test-wrapper .result a {
    padding: 0 9px;
    margin: 10px 0 0;
  }
}
.test-wrapper button:hover, .test-wrapper button.active,
.test-wrapper .result__link:hover,
.test-wrapper .result__link.active,
.test-wrapper .result a:hover,
.test-wrapper .result a.active {
