.age-step {

    button.next {
        background-color: $red;
        border-color: $red;
        color: $white;
        min-width: 100px;

        &:hover {
            background-color: lighten($red, 10%);
        }
    }

    button.disabled {
        background-color: $disabled-background-color;
        border-color: $disabled-background-color;
        color: $disabled-color;

        &:hover {
            background-color: $disabled-background-color;
            cursor: default;
        }
    }
    

    label {
        display: block;
        margin-bottom: 10px;
    }

    input {
        font-size: 2em;
        width: 3em;
        border-radius: 100px;
        border: 1px solid grey;
        display: block;
        text-align: center;
        margin: auto;
    }

    input[type=number]::-webkit-inner-spin-button {
        -webkit-appearance: none;
    }

    .message {
        margin: 20px 20px;
        color:  $red;
    }
}