.test-wrapper {
  .result {
    text-align: left;

    &__group--basis,
    &__group--1,
    &__group--2,
    &__group--5 {
      .price {
        font-weight: bolder;
        .price-sub-text {
          color: black;
          font-weight: normal;
          font-size: 18px;
        }
      }

      p {
        line-height: 1.4;
      }

      h4 {
        font-weight: normal;
      }

      .secondary-group-name {
        font-weight: bolder;
      }

      .button-wrapper {
        margin-top: 1rem;
        display: inline-flex;
        width: 100%;
        flex-wrap: nowrap;
        justify-content: center;

        @media screen and (max-width: 620px) {
          flex-direction: column;
        }

        .primary {
          background-color: $red;
          border-color: $red;
          font-weight: bolder;
          color: white;

          &:hover {
            background-color: lighten($red, 10%);
            border-color: lighten($red, 10%);
            -webkit-transition: border-color background-color 100ms ease-in-out;
            -moz-transition: border-color background-color 100ms ease-in-out;
            transition: border-color background-color 100ms ease-in-out;
          }
        }

        .secondary {
          background-color: black;
          border-color: black;
          font-weight: bolder;
          color: black;
        }
      }
    }

    &__group--basis {
      .price {
        color: #009a93;
      }
    }

    &__group--1 {
      .price {
        color: #e6007e;
      }
    }

    &__group--2 {
      .price {
        color: #5c2483;
      }
    }

    &__group--5 {
      .price {
        color: #009fe3;
      }
    }
  }
}
