@import "variables.scss";
@import "status-bar.scss";
@import "result.scss";
@import "age-step.scss";

.test-wrapper {
  padding: 100px 40px 0;
  border: solid 3px $grey-dark;
  background-color: none;
  position: relative;
  overflow: hidden;
  height: 660px;

  .test-wrapper-body {
    text-align: center;
  }

  .transition {
    -webkit-transition: opacity 900ms ease-in-out;
    -moz-transition: opacity 900ms ease-in-out;
    -ms-transition: opacity 900ms ease-in-out;
    -o-transition: opacity 900ms ease-in-out;
    transition: opacity 900ms ease-in-out;
    opacity: 0;
    &.entered {
      opacity: 1;
    }
    &.exiting {
      opacity: 0;
    }
  }

  h2 {
    font-size: $font-xxl;

    font-weight: 700;
    margin-top: 0;
    margin-bottom: 20px;
    line-height: 1.4;
  }

  p {
    margin-bottom: 20px;
    margin-top: 0;
    font-size: $font-l;
    line-height: 1.8;

    @media screen and (max-width: 890px) {
      font-size: $font-m;
      line-height: 1.4;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &--intro,
  &--step,
  &--result {
    padding: 0 0 60px;
  }

  button,
  .result__link,
  .result a {
    -webkit-transition: background-color 100ms ease-in-out;
    -moz-transition: background-color 100ms ease-in-out;
    transition: background-color 100ms ease-in-out;
    border: solid 2px;
    border-radius: 100px;
    display: inline-block;
    padding: 0 20px;
    font-size: 0.75rem;
    line-height: 36px;
    font-weight: 700;
    text-decoration: none;
    min-width: 200px;
    text-align: center;
    margin: 20px 10px 0;
    cursor: pointer;
    outline: none;
    border-color: $black;
    color: $black;
    background-color: transparent;

    @media screen and (max-width: 620px) {
      padding: 0 9px;
      margin: 10px 0 0;
    }

    &:hover,
    &.active {
      background-color: rgba($black, 0.1);
    }
  }

  .button-back {
    position: absolute;
    top: 20px;
    left: 20px;
    text-align: left;
    display: inline-block;
    font-weight: 700;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 13px;
    line-height: 1.1;
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
    color: $grey-selvbetjening;
    border: none;
    white-space: nowrap;
    background: none;
    padding: 2px;

    &:hover {
      &::before {
        -webkit-transform: translateX(-3px);
        transform: translateX(-3px);
      }
    }
    &::before {
      content: "";
      height: 16px;
      width: 16px;
      display: inline-block;
      background-image: url("data:image/svg+xml;base64,");
      background-repeat: no-repeat;
      background-position: 50%;
      background-size: contain;
      vertical-align: sub;
      margin-right: 8px;
    }

    &::before {
      -webkit-transition: -webkit-transform 0.2s ease-in-out;
      transition: -webkit-transform 0.2s ease-in-out;
      transition: transform 0.2s ease-in-out;
      transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
    }
  }

  &--intro button,
  .result__link--signup,
  .result p:last-of-type a:last-of-type {
    background-color: $red;
    border-color: $red;
    color: $white;
    min-width: 100px;
    &:hover {
      background-color: lighten($red, 10%);
    }

    span {
      height: auto;
      display: inline-flex;
      min-width: 100px;
      align-items: center;
      margin: 0;
      justify-content: center;
    }
  }

  .fade-in {
    animation: fadeIn 1.5s ease-out;
  }

  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
}

.test-wrapper--intro {
  top: 0px;
  --space-y-reverse: 0;
  margin-top: calc(3rem * calc(1 - var(--space-y-reverse)));
  margin-bottom: calc(3rem * var(--space-y-reverse));
}

.Question,
.test-wrapper--result {
  top: 50px;
}

.test-wrapper--intro,
.Question,
.test-wrapper--result {
  position: absolute;
  left: 0;
  right: 0;
  padding: 0 40px;
}

.test-wrapper--intro-illustration {
  background-image: url("images/Kvinde-med-baerbar-i-skodet.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  display: inline-block;
  transition: background 0.4s;
  width: 100%;
  height: 300px !important;
  margin-bottom: 24px;
  @media screen and (min-width: 700px) {
    height: 400px;
  }
}

@media screen and (max-width: 620px) {
  .test-wrapper {
    padding: 20px 20px;
    min-height: 700px;

    &--step,
    &--result {
      padding: 20px 20px;
    }

    &--status {
      padding: 20px 0 0;

      li {
        margin-right: 0;
      }
    }
  }
}
